import { forwardRef } from 'react';
import { CheckboxProps } from './types';
import { CheckIcon } from '../icons';
import classNames from 'classnames';
import { CheckboxContainer, CheckboxInner, MockCheckbox, StyledInput, StyledLabel } from './styled-elements';

export const Checkbox = forwardRef<HTMLInputElement, CheckboxProps>((props, ref) => {
	const { label, ...checkboxProps } = props;

	return (
		<CheckboxContainer>
			<CheckboxInner>
				<StyledInput {...checkboxProps} ref={ref} type="checkbox" />
				<MockCheckbox>
					<CheckIcon color="inherit" />
				</MockCheckbox>
			</CheckboxInner>
			{label && (
				<StyledLabel htmlFor={props.id} className={classNames({ disabled: checkboxProps.disabled })}>
					{label}
				</StyledLabel>
			)}
		</CheckboxContainer>
	);
});
